body {
    background: #F0F0E3;
}

#jumbotron {
    width: 105vw;
    height: 45vh;
    left: 0px;
    top: 0px;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), 
                rgba(0, 0, 0, 0.5)), 
                linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #000000 100%), 
                url(/static/media/hero.fe6ac337.jpg);
    background-size: 100%;
    background-position: center;
}

#jumbo-logo {
    max-width: 500px;
    height: 40vh;
}

#jumbo-text {
    height: auto;

    font-size: 4.5vw;

    color: #F0F0E3;
}

#jumbo-counter {
    width: 895px;
    height: auto;

    font-size: 4.5vw;

    color: #F0F0E3;
}

.button {
    background: transparent;
    border: 2px solid #F0F0E3;
    box-sizing: border-box;
    border-radius: 12px;

    color: #F0F0E3;
    font-size: 1rem;
}

#login-btn {
    width: 127px;
    height: auto;
}

#signup-btn {
    width: 127px;
    height: auto;
}

@media only screen and (max-width: 900px) {

    #jumbotron {
        background-size: 225%;
        height: 40vh;
    }

    #jumbo-text {
        display: none;
    }

    #jumbo-counter {
        display: none;
    }

    #jumbo-logo {
        height: 200px;
    }

    #login-btn {
        width: 127px;
        height: auto;
    }
    
    #signup-btn {
        width: 127px;
        height: auto;
    }

}


#search-bar {
    background: #FFFFFF;
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
}

#search-bar::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #b6b6b6;
    opacity: 1; /* Firefox */
}

#search-bar::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #b6b6b6;
    opacity: 1; /* Firefox */
}

#search-bar::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #b6b6b6;
    opacity: 1; /* Firefox */
}
  
#search-bar:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #b6b6b6;
}
  
#search-bar::-ms-input-placeholder { /* Microsoft Edge */
    color: #b6b6b6;
}
.img-container {
    position: relative;
}

.img-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 200px;
    z-index: 3;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.53), rgba(0, 0, 0, 0.53)), linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.53) 100%);
}

#drink-image {
    height: 200px;
    object-fit: cover;
}

#latest-price {
    font-style: normal;
    font-weight: normal;
    font-size: 96px;
    line-height: 200px;

    color: #F0F0E3;
}

.stars {
    height: 40px;
}


